/*
 *  Mobile  Navigation
 *    Styles for the header mobile navigation.
 *
 *  Version:
 *    1.0.0 - 2021/08/22
 */

.c-navigation-mobile {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  z-index: $s-z--mobile-menu;

  &__inner {
    @include t-anim--transition((padding, height), $s-anim--time-med, ease);
    padding: 0;
    height: 0;
    background-color: $s-color--background-subdued;
    overflow: hidden;

    .is-active & {
      padding: 1.5rem 0 3rem;
      height: 100vh;
    }
  }

  &__curve {
    display: block;
    width: 100%;
    max-width: 18.1875rem;
    margin: 0 auto;
    pointer-events: none;
    transform: translateY(-0.1875rem);
  }

  &__container {
    @include t-anim--transition(opacity, $s-anim--time-short, linear);
    position: relative;
    height: 100%;
    opacity: 0;

    .is-active & {
      @include t-anim--transition(opacity, $s-anim--time-short, linear, $s-anim--time-short);
      opacity: 1;
    }

    &::after {
      position: absolute;
      bottom: 0;
      left: 0;
      display: none;
      width: 100%;
      height: 2.5rem;
      background: linear-gradient(to bottom, rgba($s-color--background-subdued, 0), rgba($s-color--background-subdued, 1));
      pointer-events: none;
      z-index: $s-z--text-overlay;
      content: "";

      .is-active & {
        display: block;
      }
    }
  }

  &__overflow {
    padding: 0 $s-gutter--edge-mobile;
    height: 100%;
    overflow: auto;
    scrollbar-width: none;

    &::-webkit-scrollbar {
      display: none;
    }
  }

  // Search form
  &__search-form {
    padding: 0;
    margin-bottom: 1.25rem;

    .c-search-form {
      &__form {
        position: relative;
      }

      &__close {
        display: none
      }

      &__input {
        @include t-font--heading-6;
        padding-bottom: 0.75rem;
        padding-right: 1.5rem;
        margin-top: 0;
        border-bottom: 0.125rem solid $s-color--background-focus;
        color: $s-color--font-subdued;

        &:focus {
          border-color: $s-color--border-focus;
        }
      }

      &__btn {
        @include t-absolute-center-y();
        right: 0;
        width: 1.5rem;
        height: 1.5rem;
        font-size: 0;
        background: url(../../assets/icon-arrow-right-alt.svg) no-repeat right center/1.5rem;
      }
    }
  }

  &-menu-groups {
    padding-bottom: 5.5rem;
  }

  // Menu groups
  &__menu-group {
    position: relative;
    padding-bottom: 1.625rem;
    margin-bottom: 1.5rem;

    &:last-child {
      margin-bottom: 0;
    }

    &::after {
      position: absolute;
      left: 0;
      bottom: 0;
      height: 0.125rem;
      background-color: $s-color--background-focus;
      content: "";
    }

    &:nth-child(1) {
      padding-bottom: 1.125rem;
      margin-bottom: 1.125rem;

      &::after {
        width: 10.5rem;
      }
    }

    &:nth-child(2)::after {
      width: 4.5rem;
    }

    &:nth-child(3)::after {
      display: none;
    }
  }

  // Level 1
  &__lvl1 {
    padding: 0;
    margin: 0;
    list-style: none;

    &-title-link {
      position: relative;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0.75rem 0;

      &::after {
        @include t-anim--transition(transform, $ease: linear);
        display: block;
        width: 1rem;
        height: 1rem;
        background: url(../../assets/icon-arrow-right-alt.svg) no-repeat right center/1rem;
        content: "";

        .is-open & {
          transform: rotate(-180deg);
        }
      }

      .has-submenu &::after {
        background: url(../../assets/icon-caret-down-alt.svg) no-repeat right center/1rem;
      }
    }

    &-link-label {
      font-weight: $s-font--root-weight;
    }

    &-text-link {
      display: block;
      padding: 0.5rem 0;
    }

    &-item {
      &.is-currency {
        margin-top: 0.125rem;
        display: flex;
        align-items: center;
        border: 1px solid $s-color--background-focus;
        width: 4.6875rem;
      }
    }
  }

  &__currency {

    &-select {
      @include t-font--paragraph-2;
      padding: 0.5rem 1rem 0.5rem 0.3125rem;
      border: none !important;
      outline: none !important;
      background-color: transparent !important;

      &:focus {
        border-bottom: 1px solid $s-color--border-progress-fill !important;
      }
    }

    &-icon {
      width: 0.75rem;
      height: 0.75rem;
      transform: rotate(268deg);

      path {
        stroke: $s-color--background-focus;
        opacity: 1;
      }
    }
  }

  // Level 2
  &__lvl2 {
    display: none;
    padding: 0.75rem 0 1.25rem;
    margin: 0;
    list-style: none;
    flex-direction: column;

    &.has-illustrations {
      display: none;
      flex-direction: row;
      justify-content: flex-start;
      flex-wrap: wrap;
      gap: 1rem;
    }

    &-link {
      display: block;
      padding: 0.5rem 0;

      .has-illustrations & {
        text-align: center;
        padding: 0;
      }
    }

    &-illustration {
      width: 5rem;
      height: 5rem;

      &-item {
        width: 6.25rem;
      }

      &-container {
        display: block;
      }

      &-label {
        display: block;
      }
    }
  }

  /*** Media Queries ***/
  @media screen and (min-width: 34.375rem) {
    &__lvl1 {
      &-title-link {
        display: inline-flex;
        gap: 0 0.75rem;
      }
    }

    &__lvl2 {
      flex-direction: row;
      flex-wrap: wrap;
      gap: 0 1rem;

      &-item {
        width: calc(50% - 0.5rem);
      }
    }
  }

  @include t-media-query($s-small-up) {
    &__lvl2-item {
      width: calc(33.33% - 1rem);
    }
  }
}