/*
 *  Desktop Navigation
 *    Styles for the header desktop navigation.
 *
 *  Version:
 *    1.0.0 - 2021/08/22
 */

.c-navigation-desktop {
  &__links {
    display: flex;
    padding-left: 0;
    margin: 0;
    list-style: none;
  }

  // Level 1
  &__lvl1 {
    &-link {
      @include t-anim--transition(color, $ease: linear);
      position: relative;
      display: block;
      padding-top: 2.2969rem;
      padding-bottom: 2.2969rem;
    }

    &-item {
      padding-right: 1rem;

      &:first-child:hover {
        .c-navigation-desktop__lvl2-inner {
          max-height: 37.5rem;
        }
      }

      &:hover {
        &:after {
          content: "";
          display: block;
          width: 12.5rem;
          height: 2rem;
          background: transparent;
          height: 2rem;
          position: absolute;
          transform: translateX(-36%);
          top: 4.3125rem;
        }
        .c-navigation-desktop__lvl1-link {
          color: $s-color--border-focus;
        }

        .c-navigation-desktop__lvl2-inner {
          max-height: 4.375rem;
          pointer-events: initial;

          .is-active & {
            transition-delay: $s-anim--time-med;
          }
        }
      }

      &.has-submenu {
        .c-navigation-desktop__lvl1-link {
          position: relative;
          padding-right: 0.875rem;
          background: url(../../assets/icon-chevron-down.svg) no-repeat center right/#{0.375rem};
        }

        &:hover {
          .c-navigation-desktop__lvl1-link {
            background: url(../../assets/icon-chevron-down-orange.svg) no-repeat center right/#{0.375rem};
          }
        }
      }

      &:last-child {
        padding-right: 0;
      }

      &.has-submenu.is-active {
        .c-navigation-desktop__lvl1-link::after {
          display: block;
        }

        .c-navigation-desktop__lvl2 {
          pointer-events: initial;
        }
      }
    }
  }

  // Curve
  &__curve {
    display: block;
    width: 100%;
    max-width: 24.875rem;
    margin: 0 auto;
    pointer-events: none;
    transform: translateY(-0.3125rem);
  }

  // Level 2
  &__lvl2 {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    pointer-events: none;

    &-inner {
      @include t-anim--transition(
        max-height,
        $s-anim--time-med,
        $ease: cubic-bezier(0.357, 0.096, 0.006, 1.161)
      );
      padding: 0 min(6%, 7.5rem);
      overflow: hidden;
      background-color: $s-color--background-subdued;
      pointer-events: none;
      max-height: 0;
      transition-delay: 0s;
    }
  }

  // Columns layout
  &__cta {
    display: inline-flex;
    align-items: center;
    gap: 0 0.6rem;

    &::after {
      display: block;
      width: 1.625rem;
      height: 1.625rem;
      content: "";
      background: url(../../assets/icon-arrow-right-alt.svg) no-repeat right
        center/100%;
    }

    &-container {
      position: relative;
      padding-bottom: 1.5625rem;

      &::after {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 6.8125rem;
        height: 0.0625rem;
        background-color: $s-color--border-subdued-20;
        display: block;
        content: "";
      }
    }
  }

  &__columns {
    display: grid;
    grid-template-columns: 1fr 0.0625rem 1fr 0.0625rem 1fr 0.0625rem 1fr;
    gap: 0 2rem;
    padding: 1.5rem 0 2.4rem;
  }

  &__column {
    &:first-child {
      padding-left: 0;
    }

    &:last-child {
      padding-right: 0;
    }

    &-seperator {
      background-color: $s-color--border-subdued-20;
    }

    &-heading {
      margin-bottom: 0.9rem;
    }

    &-links {
      display: grid;
      grid-template-columns: 1fr;
      gap: 0.5rem 1.25rem;
      padding: 0;
      margin: 0;
      list-style: none;
    }
  }

  &__illustration {
    width: 3.25rem;
    height: 3.25rem;

    &-links {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 0.9rem 1.5rem;
      padding: 0;
      margin: 0;
      list-style: none;
    }

    &-item {
      min-width: 3.25rem;
    }

    &-link {
      display: block;
      text-align: center;
    }

    &-container {
      display: block;
    }

    &-label {
      display: block;
    }
  }

  // Row layout
  &__row {
    padding-bottom: 1.575rem;

    &-links {
      display: flex;
      flex-wrap: wrap;
      gap: 0 1.25rem;
      padding: 0;
      margin: 0;
      list-style: none;
    }
  }

  /*** Media Queries ***/

  @include t-media-query($s-medium-up) {
    &__lvl2 {
      display: block;
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__lvl1 {
      &-item {
        padding-right: 2rem;

        &:hover:after{
          height: 3.5rem;
        }
      }

      &-link {
        padding-top: 2.9375rem;
        padding-bottom: 2.9375rem;
      }
    }

    &__curve {
      max-width: 41.3125rem;
      transform: translateY(-0.4375rem);
    }

    &__cta {
      gap: 0 1rem;

      &::after {
        width: 2rem;
        height: 2rem;
      }

      &-container {
        padding-bottom: 2.5625rem;
      }
    }

    &__columns {
      padding: 2.5rem 0 4rem;
      gap: 0 3rem;
    }

    &__column {
      &-heading {
        margin-bottom: 1.5rem;
      }

      &-links {
        grid-template-columns: repeat(2, 1fr);
        gap: 1rem;
      }
    }

    &__illustration {
      width: 5.5rem;
      height: 5.5rem;

      &-links {
        grid-template-columns: repeat(3, 1fr);
        gap: 1.5rem 2.5rem;
      }

      &-item {
        width: 5.5rem;
      }

      &-container {
        margin-bottom: 0.5rem;
      }
    }

    &__row {
      padding-bottom: 2.625rem;

      &-links {
        gap: 0 2rem;
      }
    }
  }

  @include t-media-query($s-xxlarge-up) {
    &__columns {
      gap: 0 4rem;
    }

    &__column {
      &-links {
        gap: 1rem 3rem;
      }
    }
  }
}
