/*
 *  Header
 *    Styles for the header section.
 *
 *  Version:
 *    1.0.0 - 2021/08/13
 */

.c-header {
  height: 8.125rem;

  // Utility
  .is-desktop {
    display: none;
  }

  // Promobar
  &__promobar {

    background-color: $s-color--background-focus;
    height: 4rem;

    &-inner {
      position: relative;
      padding: 0 calc((100vw - 340px)/2);
      width: 100%;
      height: 100%;
    }

    &-navs {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      height: 100%;
    }

    &-nav {
      cursor: pointer;

      &.is-prev {
        left: 0;
      }

      &.is-next {
        right: 0;
      }

      .is-no-carousel & {
        visibility: hidden;
      }
    }

    &-icon {
      height: 0.5rem;
    }

    &-carousel {
      position: absolute !important;
      inset: 0 calc((100vw - 325px)/2);
    }

    &-slide {
      transition: opacity 0.5s ease-in-out;

      &:not(.keen-slider__slide) {
        opacity: 0
      }
    }

    &-text {
      height: 100%;
      display: block;
      margin: 0;
      text-align: center;
      display: flex;
      justify-content: center;
      align-content: center;
      align-items: center;
    }
  }

  // Main Header
  &__inner {
    position: relative;
    display: flex;
    justify-content: space-between;
    padding: 1.3438rem $s-gutter--edge-mobile;
    background-color: $s-color--background-subdued;
    z-index: $s-z--sticky-header;

    &.is-sticky {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      transform: translateY(-100%);
      filter: drop-shadow(0.0625rem 0.0625rem 0.1875rem $s-color--header-shadow);
      opacity: 0;
    }

    &.is-sticky.is-waiting {
      @include t-anim--transition((transform, opacity), $s-anim--time-med);
    }

    &.is-sticky.is-visible,
    .is-scroll-disabled &.is-sticky {
      transform: translateY(0);
      opacity: 1;
    }
  }

  &__curve {
    @include t-absolute-center-x;
    top: 100%;
    display: block;
    width: 77.6%;
    content: "";
  }

  &__currency {
    display: none;
  }

  // Actions
  &__actions {
    display: flex;
  }

  // Search
  &__search-form {
    display: none;
  }

  &__cart-container {
    display: inline-flex;
    justify-content: center;
    align-items: center;
  }

  &__action {
    @include t-anim--transition(opacity, $ease: linear);
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 1.625rem;
    height: 1.625rem;
    padding: 0.25rem;
    margin-right: 0.6563rem;
    cursor: pointer;

    &:last-child {
      margin-right: 0;
    }

    &.is-cart {
      position: relative;
      padding: 0.0938rem;

      &.is-shaking{
        animation: shaking 1s ease-out;
        animation-iteration-count: infinite;

        .c-header__count{
          animation: scaleUp 1s ease-in-out;
        }
      }

    }

    .is-active &:not(.is-mobile-trigger) {
      opacity: 0;
      pointer-events: none;
    }

    &.is-mobile-trigger {
      position: relative;

      svg {
        @include t-anim--transition(opacity, $ease: linear);
        @include t-absolute-center-x-y;
        width: 100%;
        height: 100%;
      }

      .is-hamburger {
        opacity: 1;
        pointer-events: initial;
        max-height: 0.625rem;
      }

      .is-close {
        opacity: 0;
        pointer-events: none;
      }

      .is-active & {
        .is-hamburger {
          opacity: 0;
          pointer-events: none;
        }

        .is-close {
          opacity: 1;
          pointer-events: initial;
        }
      }
    }
  }

  // Icons
  &__icon {
    width: 100%;

    &.is-hamburger {
      height: 0.625rem;
    }
  }

  &__count {
    display: none;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: -0.25rem;
    right: -0.25rem;
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    font-size: 0.625rem;
    color: $s-color--font-invert;
    background-color: $s-color--background-impact;

    &.is-visible {
      display: inline-flex;
    }
  }

  // Center - Logo
  &__center {
    @include t-absolute-center-x-y;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__logo {
    display: flex;
    width: 7.6875rem;
    height: 2.9375rem;
    transform: translateY(0.3125rem);

    &-svg {
      width: 100%;
    }
  }

  /*** Media Queries ***/

  @include t-media-query($s-medium-up) {
    height: 9.3125rem;

    .is-desktop {
      display: block;
    }

    .is-mobile {
      display: none;
    }

    &__inner {
      padding: 0 clamp(#{$s-gutter--edge-mobile}, 6%, 7.5rem);
    }

    &__logo {
      width: 11.375rem;
      height: 4.375rem;
    }

    &__right {
      display: flex;
      align-items: center;
      padding: 2.2rem 0;
    }

    &__currency {
      display: block;
      margin-right: 1.375rem;
      transform: translateY(0.0625rem);

      &-select {
        margin-bottom: 0;
      }
    }

    &__cart-container {
      position: relative;
    }

    &__search-form {
      @include t-anim--transition((border-color, opacity), $ease: linear);
      @include t-absolute-center-y;
      right: calc(100% + 1.0781rem);
      display: block;
      width: 18.75rem;
      height: 2.625rem;
      background-color: $s-color--background-subdued;
      z-index: $s-z--text-overlay;
      border-radius: 0.5rem;
      overflow: hidden;
      border: 0.0625rem solid transparent;
      opacity: 0;
      pointer-events: none;

      &.is-active {
        opacity: 1;
        pointer-events: initial;
      }

      &:focus-within {
        border-color: $s-color--border-focus;
      }

      .c-search-form {
        &__form {
          display: flex;
          height: 100%;
          background-color: $s-color--background-focus;
        }

        &__input {
          @include t-font--paragraph-2;
          height: 100%;
          margin-top: 0;
          order: 1;
          color: $s-color--font-subdued;

          &::placeholder {
            font-size: inherit;
            color: inherit;
          }
        }

        &__btn {
          order: 0;
          width: 3.4375rem;
          height: 100%;
          background: url(../../assets/icon-search.svg) no-repeat center center/1.125rem;
          cursor: pointer;
        }

        &__close {
          order: 2;
          width: 3.4375rem;
          height: 100%;
          background: url(../../assets/icon-close.svg) no-repeat center center/1.125rem;
          cursor: pointer;
        }
      }
    }

    &__action {
      width: 1.7891rem;
      height: 1.7891rem;
      margin-right: 1.0781rem;

      &.is-desktop {
        display: inline-flex;
      }

      .is-active &:not(.is-mobile-trigger) {
        opacity: 1;
        pointer-events: initial;
      }
    }

    &__promobar {
      &-inner {
        padding: 0 calc((100vw - 650px)/2);
      }

      &-icon {
        width: 0.5rem;
        height: 0.8331rem;
      }

      &-carousel {
        inset: 0 calc((100vw - 640px)/2);
      }
    }

    &__count {
      top: 0.0625rem;
      right: 0;
    }
  }

  @include t-media-query($s-xlarge-up) {
    height: 10.6875rem;

    &__right {
      padding: 2.805rem 0;
    }

    &__logo {
      width: 15.0625rem;
      height: 5.8125rem;
      transform: translateY(0.625rem);
    }

    &__currency {
      margin-right: 2rem;
      transform: translateY(0.0625rem);
    }

    &__search-form {
      width: 22.5rem;
      height: 3.25rem;

      .c-search-form {
        &__btn {
          width: 4.5625rem;
          background-size: 1.5rem;
        }

        &__input {
          width: calc(100% - 8.3125rem);
        }

        &__close {
          width: 3.75rem;
          background-size: 1.5rem;
        }
      }
    }

    &__action {
      width: 1.9531rem;
      height: 1.9531rem;
      margin-right: 1.5rem;

      &.is-wishlist {
        width: 2.0137rem;
        height: 1.9531rem;
        margin-right: 1.7813rem;
      }

      &.is-cart {
        width: 2rem;
      }
    }
  }
}

@keyframes shaking {
  0% { transform: rotate(0deg); }
  15% { transform: rotate(15deg); }
  30% { transform: rotate(-15deg); }
  45% { transform: rotate(15deg); }
  60% { transform: rotate(-15deg); }
  75% { transform: rotate(15deg); }
  90%  { transform: rotate(-15deg); }
  100% { transform: rotate(0deg); }
}


@keyframes scaleUp {
  0% { transform: scale(1); }
  50% { transform: scale(1.5); }
  100% { transform: scale(1); }
}